let page = 1
export default {
    data(){
        return {
            coponIndex: 1,
            coponList:[],
            loadingType:0
        }
    },
    onShow(){
        
    },
    created() {
        this.getcouponlstshop()
    },
    onReachBottom:function(){
        // 上拉加载更多
        if(this.loadingType == 1 || this.loadingType == 2) { return false }
        this.loadingType = 1

        page++
        this.getcouponlstshop()
	},
    methods: {
        getcouponlstshop(){
            this.$http.getcouponlstshop({filter:this.coponIndex,page:page}).then(res=>{
                if(res.status == 200) {
                    if(res.data.length == 0) {
                        this.loadingType = 2
                        return false
                    }
                    this.coponList = [...this.coponList,...res.data]
                    this.loadingType = 0
                }
            })
        },
        clickactive(i) {
            if(this.coponIndex == i) return false
            this.coponIndex = i
            page = 1
            this.coponList = []
            this.getcouponlstshop()
        }
    },
}
